import React from 'react';
import { connect } from 'react-redux';
import { NavBar, ActivityIndicator } from 'antd-mobile';
import ReactDOM from 'react-dom';
import Content from '../../views/common/Content';
import NativeAppScreen from '../common/NativeAppScreen';
import { 
    goBackAction, 
    setScrollerPositionAction,
} from '../../store/actions/overview/listAction';

import '../../assets/css/overview.css';
// import overviewList from '../../assets/config/overview.js';
import LeftContentView from '../../views/common/LeftContentView';

class OverviewListScreen extends NativeAppScreen
{
    constructor(props)
    {
        super(props);
        this.state = {
            isFetching: true,
        };
    }
    componentDidMount()
    {
        this.loadData();

        this.updateNativeTitle('禹州概览');
    }
    render()
    {
        let overviewList = this.state.overviewList;
        return (
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark"
                            className="navBar"
                            leftContent={<LeftContentView
                                ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                                goBackAction={goBackAction}
                                history={this.props.history}
                            />}
                            onLeftClick={() => {
                                this.LeftContentView.goHome();
                            }}>
                            禹州概览
                        </NavBar>
                }
                <Content ref={(view) => { this.scrollView = view ? view.getWrappedInstance() : this.scrollView; }}>
                    {
                        overviewList ? overviewList.map((item, index) => this.renderOverView(item, index, index === overviewList.length - 1)) : null
                    }
                    {
                        this.state.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                </Content>
            </div>
        );
    }
    renderOverView(item, index, isLast)
    {
        let bgWidth = window.innerWidth - 20;
        let bgHeight = bgWidth * 400 / 710;
        return (
            <div key={index}
                className={isLast ? 'overviewListItem overviewListLastItem' : 'overviewListItem'} 
                style={{backgroundImage: `url(${item.icon})`, height: bgHeight}}
                onClick={()=>this.showDetail(index)}>
            </div>
        );
    }
    loadData()
    {
        import('../../assets/config/overview.js').then(({ overviewList })=>{
            this.setState({
                overviewList,
                isFetching: false,
            }, ()=>{
                this.restoreScrollViewPosition();
            });
        });
    }
    showDetail(index)
    {
        this.cacheScrollOffset();
        this.props.history.push(`/overview/detail/${index}`);
    }
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.scrollView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(offsetY));
    }
    restoreScrollViewPosition()
    {
        if(this.scrollView)
        {
            if(!isNaN(this.props.scrollViewOffset))
            {
                this.scrollView.scrollToY(this.props.scrollViewOffset);
            }
            else
            {
                this.scrollView.scrollToY(0);
            }
        }
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.overview.listScreen;
    return {
        ...obj,
        isFromNativeApp: state.native.isFromNativeApp
    };
};

OverviewListScreen = connect(mapStateToProps)(OverviewListScreen);

export default OverviewListScreen;


